// HEADER

.home{

    .hero-container{
        height:600px;
        width:100vw;
        display:flex;
        justify-content:center;
        position:relative;
        z-index:10;
            @include desktop{
                height:100vh;
            }
    }

    @keyframes fade-in{
        0%{
            opacity:0;
        }
        100%{
            opacity:1;
        }
    }

    .home-hero{
        object-fit:cover;
        z-index:-1;
        max-width:100%;
}

    .social-icons-container{
    height:80px;
    width:100%;
    background-color:$main__yellow;
    display:flex;
    justify-content:center;
    align-items:center;
        box-shadow: 0 -12px 69px -3px white;
    i{
    color:black;
    padding:1rem;
    font-size:1.5rem;
    height:50px;
    width:50px;
    }
    }

    .general-button{
        @include general-button;
        
    }

    .hero-button{
        align-self: flex-end;
        position: absolute;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
        justify-content: center;
        -ms-flex-align: center;
        align-items: center;
