                flex-wrap:wrap;
                padding:0 2rem;
            }
    }

    .news-post-container{
        position:relative;
        padding-bottom: 1.6rem;
        margin-top:4rem;
        padding-top:1rem;
        border-top: 5px solid rgba(245, 234, 155, 0.31);
        border-radius:5px;
            @include desktop{
