            }
            img{
                width:120px;
            }
    }

    .news-title{
        position: absolute;
    left: 70%;
    font-size: 1.5rem;
    bottom: 16rem;
            @include desktop{
                left: 19rem;
            bottom: 26rem;
            font-size: 2.5rem;
            }
            a{
                color:white;
                text-decoration:none;
                text-transform:uppercase;
                font-family:'montserrat';
                border-radius:8px;
            }
    }
    .front-news-image-container{
    max-height: 200px;
        position:relative;
        transition:all 0.2s linear;
            @include desktop{
                max-height:350px;
            }
            img{
                width:100%;
                height:100%;
                object-fit:cover;
            }
            &:hover{
