    .news-date{
        position: absolute;
        z-index: 10;
        margin-left: 15px;
        color: #fff;
        padding: .5rem 1.5rem;
        bottom: 12rem;
        left: 0%;
        text-transform: uppercase;
        width: 200px;
        bottom: 15px;
    }

    .news-excerpt-container{
        display:flex;
        justify-content:space-between;
            @include desktop{
                flex-direction:column;
            }
            p{
                color:white;
                font-size:14px;
                font-family:'montserrat';
                max-width:80vw;
                    @include desktop{
                    font-size: 1.2rem;
                    max-width: 100%;
                    }
            }
    }

    .read-more-news{
        align-self:flex-end;
        text-decoration:none;
        background-color:$main__yellow;
        width:200px;
        height:50px;
        text-transform:uppercase;
        color:black;
        border-radius:15px;
        overflow:hidden;
        position:relative;
        transition:all 0.2s ease;
        display: flex;
        font-family:'montserratmedium';
            span{
                z-index:10;
                margin: 0 auto;
            margin-top: 13.5px;
                font-family:'montserratmedium';
            }
            &:hover{
                color:black;
                transform:translateY(-10px);
            }
    }

    .read-more-news:before {
        content:'';
        position: absolute;
        width: 0px;
        height: 100px;
        background-color: white;
        color:black;
        transition: all 0.5s ease;
    }

    .read-more-news:hover:before{
        width:100%;
        border-radius:15px;
    }

    .news-post-container:first-child{
        border-top: 9px solid rgba(245, 234, 155, 0.76);
    padding-top: 2rem;
    border-radius: 5%;
            @include desktop{
                border-radius:5px;
                padding-top:1rem;
                border-top: 5px solid hsla(53,82%,78%,.31)
            }
    }

    .news-post-container:last-child{
        border-bottom: 9px solid rgba(245, 234, 155, 0.76);
    margin-top: 2rem;
    border-radius: 5%;
            @include desktop{
                margin-top:auto;
                border-radius:5px;
                border-top: 5px solid rgba(245, 234, 155, 0.31);
                border-bottom:none;
            }
    }

    .fadein{
        opacity:0;
    }

    .news-button{
        padding: 13px 56px;
        font-family: montserratmedium;
        text-decoration: none;
        text-transform: uppercase;
        border-radius: 15px;
        border: 1px solid $main__yellow;
        color: $main__yellow;
        position:relative;
        top:3rem;
    }

    .news-button:after {
        content: "";
        position: absolute;
        top: 0px;
        left: 0px;
        width: 0%;
        height: 130%;
        background-color: rgba(255,255,255,0.4);
        -webkit-transition: none;
        -moz-transition: none;
        -ms-transition: none;
        transition: none;
        border-radius: 15px;
}

    .news-button:hover:after {
        width: 120%;
        background-color: rgba(255,255,255,0);
        -webkit-transition: all 0.3s ease-out;
        -moz-transition: all 0.3s ease-out;
        -ms-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
    }

    .news-button-container{
        display: flex;
        justify-content: center;
        background-color: #000;

    }

    //Folding Cube

    .folding-cube-container{
        position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: black;
        z-index:100;
        display:flex;
        align-items:center;
    }

    .sk-folding-cube {
        margin: 20px auto;
        width: 95px;
        height: 95px;
        position: relative;
        -webkit-transform: rotateZ(45deg);
        transform: rotate(45deg) translate(-80%,-65%);
    }

    .sk-folding-cube .sk-cube {
        float: left;
        width: 50%;
        height: 50%;
        position: relative;
        -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
        transform: scale(1.1);
    }
    .sk-folding-cube .sk-cube:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: white;
        -webkit-animation: sk-foldCubeAngle 2.4s infinite linear both;
        animation: sk-foldCubeAngle 2.4s infinite linear both;
        -webkit-transform-origin: 100% 100%;
        -ms-transform-origin: 100% 100%;
        transform-origin: 100% 100%;
    }
    
    .sk-folding-cube .sk-cube2 {
        -webkit-transform: scale(1.1) rotateZ(90deg);
        transform: scale(1.1) rotateZ(90deg);
    }
    
    .sk-folding-cube .sk-cube3 {
    -webkit-transform: scale(1.1) rotateZ(180deg);
            transform: scale(1.1) rotateZ(180deg);
    }
    
    .sk-folding-cube .sk-cube4 {
    -webkit-transform: scale(1.1) rotateZ(270deg);
            transform: scale(1.1) rotateZ(270deg);
    }
    .sk-folding-cube .sk-cube2:before {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
    }
    .sk-folding-cube .sk-cube3:before {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
    }
    .sk-folding-cube .sk-cube4:before {
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
    }
    @-webkit-keyframes sk-foldCubeAngle {
    0%, 10% {
        -webkit-transform: perspective(140px) rotateX(-180deg);
                transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    } 25%, 75% {
        -webkit-transform: perspective(140px) rotateX(0deg);
                transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    } 90%, 100% {
        -webkit-transform: perspective(140px) rotateY(180deg);
                transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
    }

    @keyframes sk-foldCubeAngle {
    0%, 10% {
        -webkit-transform: perspective(140px) rotateX(-180deg);
                transform: perspective(140px) rotateX(-180deg);
        opacity: 0;
    } 25%, 75% {
        -webkit-transform: perspective(140px) rotateX(0deg);
                transform: perspective(140px) rotateX(0deg);
        opacity: 1;
    } 90%, 100% {
        -webkit-transform: perspective(140px) rotateY(180deg);
                transform: perspective(140px) rotateY(180deg);
        opacity: 0;
    }
    }


    //Artist Carousel

    .archive-header-container{
    width:100%;
    display: flex;
    justify-content: center;
        margin-bottom:2rem;
            @include desktop{
                margin-bottom:0;
            }
            .artist-button,{
                padding: 13px 56px;
                font-family: montserratmedium;
                text-decoration: none;
                text-transform: uppercase;
                border-radius: 15px;
                border: 1px solid $main__yellow;
                color: $main__yellow;
                position:relative;
            }
    }

    .artist-button:after {
        content: "";
        position: absolute;
        top: 0px;
        left: 0px;
        width: 0%;
        height: 100%;
        background-color: rgba(255,255,255,0.4);
        -webkit-transition: none;
        -moz-transition: none;
        -ms-transition: none;
        transition: none;
}

    .artist-button:hover:after {
        width: 120%;
        background-color: rgba(255,255,255,0);
        -webkit-transition: all 0.3s ease-out;
        -moz-transition: all 0.3s ease-out;
        -ms-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;
    }


    .artist-section{
        height: 33rem;
        padding:3rem 0;
        background:url('../../images/black-background-design.jpg');
        background-size:cover;
            @include desktop{
                height:900px;
            }
    }

    .main-carousel{
        padding:0;
        margin:0;
        button{
            background-color: #000;
        height: 40px;
        border-radius: 50%;
        opacity: .6;
        box-shadow: none;
         width: 40px;
            transition:opacity 0.2s ease-in;
              &:hover{
                     opacity:1;
                 }
        }
        .previous{
            @include desktop{
                left:5%;
            }
        }
        
        .next{
            @include desktop{
                right:5%;
            }
        }
        
        .next, .previous{
            @include desktop{
                top: 40rem;
                height: 75px;
            box-shadow: none;
            width: 225px;
            border-radius: 5px;
            box-shadow: -2px 0px 51px $main__yellow;
                background-color:white;
            }
        }
    }//main-carousel

    .carousel-cell.is-selected{
        display: inline-flex;
    justify-content: center;
    align-items: center;
            @include desktop{
                margin-left:4rem;
            }
    }



    .carousel-cell.is-selected{
        .artist-header{
            animation-name:header-move;
            animation-duration:0.5s;
            animation-timing-function:linear;
            animation-delay:0.2s
        }
    }

    .carousel-cell.artist-list-item{
        display:inline-flex;
    }

    @keyframes header-move{
        from{
            transform:rotateX(0deg);
            text-shadow: -22rem 4px 25px #000;
        }
        to{
            transform:rotateX(360deg);
            text-shadow: 5px 12px 25px #000000;
        }
    }

    .artist-list-item:first-child{
        left: 59%;
    }

    .flickity-page-dots{
        @include desktop{
            display:none;
        }
    }
    
    
    .artist-content-container{
        display:none;
            @include desktop{
                display:inline-block;
            }
        p{
            position: relative;
            color: #fff;
            width: 60vw;
            max-width: 40rem;
            line-height: 2rem;
            font-size: 2rem;
            margin-left: 28px;
            display: inline-block;
            box-sizing: border-box;
            right: 100px;
        }
    }

    .carousel-cell.artist-list-item.is-selected{
        .artist-content-container{
            p{
                margin-left:9rem;
            }
        }
    }

    

